.Container {
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto; }
  @media (min-width: 36em) {
    .Container {
      max-width: 33.75rem; } }
  @media (min-width: 48em) {
    .Container {
      max-width: 45rem; } }
  @media (min-width: 62em) {
    .Container {
      max-width: 60rem; } }
  @media (min-width: 75em) {
    .Container {
      max-width: 71.25rem; } }



